import { connect } from "react-redux";

import styled from 'styled-components';

const VoteIcon = styled.i`
    font-size: 18px;
`

function Results({ items }) {
    return <div>
        {items.map(Result)}
    </div>
}

function Result() {
    return <div>
        <VoteIcon className="fas fa-arrow-up"/><br></br>
        <VoteIcon className="fas fa-arrow-down"/>
        </div>
}

export default connect()(Results)